import React from "react";
import { GiAcid, GiAbstract044, GiAbstract013, GiAbstract015 } from "react-icons/gi";

function Box(props) {
    return(
          <a className="box" href={'#' + props.id}>
            <div className="logobox" />
            <div className="headerbox">{props.header}</div>
            <div className="textbox">{props.text}</div>
          </a>
    )
}

export function BoxContainer() {
    var icons = [ 
        { header: <GiAcid />,        text: 'Info' , id: 'contact'},
        { header: <GiAbstract044 />, text: 'Empresas asistentes' , id: 'empresas'},
        { header: <GiAbstract013 />, text: 'Organizacion' , id: 'organización'},
        { header: <GiAbstract015 />, text: 'Inscripcion', id: 'inscripcion'}
    ].map((name, i) => {
        return <Box header={name.header} text={name.text} key={i} id={name.id} />
    })

    return(
        <div className="boxContainer">
            {icons}
        </div>
    )
}